import React, { useEffect, useState } from "react";
import axios from "axios";
import Nav from "../components/Nav";

const { kakao } = window;
const mapStyle = {
  width: '100vw',
  height: '100vh',
  zIndex: 11999,
  position: 'fixed',
  top: '8vh'
  // top: 0
};

//     
function makeOverListener(map, marker, infowindow) {
    return function () {
    infowindow.open(map, marker);
    };
}

//     
function makeOutListener(infowindow) {
    return function () {
    infowindow.close();
    };
}

const KakaoMap = () => {
  const [data, setData] = useState(null);
  // const [loading, setLoading] = useState(false);

  useEffect(() => {

    const fetchData = async () => {
      // setLoading(true);
      try {
        const response = await axios.get(
          "http://openapi.seoul.go.kr:8088/6a664d4c486d696c353144645a5a59/json/bikeList/2/998/",
        );
        setData(response.data.rentBikeStatus);
        console.log(response.data.rentBikeStatus)
        console.log("upload ")
      } catch (e) {
        console.log(e);
      }
      // setLoading(false);
    };
    fetchData();
  },[]);

  
  useEffect(() => {
        
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(37.531837141995744, 126.97243966282926),
      level: 5,

    };

    //map
    const map = new kakao.maps.Map(container, options);
    
    if (data && data.row) {
      data.row.forEach((el) => {
        let iwContents = 
        `<div style="padding-left : 3px;">
          <div >
            ${el.stationName.substr(4, el.stationName.length)} 
            <br/>
              : <span style="background-color:yellow; width: 40px;">${el.parkingBikeTotCnt} </span> 
            </div>
          </div>`;
        //  
        const marker = new kakao.maps.Marker({
          //   
          map: map,
          //   
          position: new kakao.maps.LatLng(el.stationLatitude, el.stationLongitude),
        });

        //    
        let infowindow = new kakao.maps.InfoWindow({
          content: iwContents  //   

        });
    
        //  mouseover  mouseout  
        //     
        //        
        kakao.maps.event.addListener(
          marker,
          "mouseover",
          makeOverListener(map, marker, infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "mouseout",
          makeOutListener(infowindow)
        );
        
      });
    }

  }, [data]);

  return(
    <>

      <div>

        <div style={mapStyle} id="map" />
      </div>
        <Nav/>

    </>
  );
}

export default KakaoMap;